import React from "react";
import rehypeReact from "rehype-react";
import { Link, graphql } from "gatsby";
import {
  Container,
  MarkdownAnchor,
  MarkdownParagraph,
  ProfileHeader
} from "@undataforum/components";
import Layout from "../components/Layout";

// External links open in new browser tab.
const externalLink = ({ href, children }) => (
  <a href={href} target="_blank" rel="noreferrer noopener">
    {children}
  </a>
);

// Internal links are handled by Gatsby.
const internalLink = ({ href, children }) => <Link to={href}>{children}</Link>;

const A = ({ href, children }) => (
  <MarkdownAnchor anchor={externalLink} href={href}>
    {children}
  </MarkdownAnchor>
);

const renderAst = new rehypeReact({
  createElement: React.createElement,
  components: {
    a: A,
    p: MarkdownParagraph
  }
}).Compiler;

const ProfileTemplate = ({
  data: {
    markdownRemark: {
      htmlAst,
      frontmatter: { firstName, lastName, committee }
    },
    imageSharp: {
      resize: { src: img }
    }
  }
}) => {
  const name = `${firstName} ${lastName}`;
  const badges = [];
  if (committee) {
    badges.push({
      text: "Committee Member",
      href: "/committee/",
      color: "red"
    });
  }
  return (
    <Layout>
      <Container maxWidth={6} mt={3}>
        <ProfileHeader
          anchor={internalLink}
          name={name}
          img={img}
          badges={badges}
          mb={3}
        />
        {renderAst(htmlAst)}
      </Container>
    </Layout>
  );
};

export default ProfileTemplate;

export const templateQuery = graphql`
  query Profile($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      htmlAst
      frontmatter {
        firstName
        lastName
        committee
      }
    }
    imageSharp(fields: { slug: { eq: $slug } }) {
      resize(width: 500, height: 500) {
        src
      }
    }
  }
`;
